@import url(https://cdn.jsdelivr.net/npm/bootstrap@4.6.0/dist/css/bootstrap.min.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.3/css/all.min.css);
.header-prime{
    width: 100%;
    position: fixed;
    background-color: #EC1D24;
    z-index: 100;
}

nav {

    width: 95%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    font-family: 'Marvel', sans-serif;
}

.logo{
    height: 100px;
}

.header-actions{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;

    min-width: 20%;
}

.total-hq{
    font-size: 1.5rem;
    color: #fff;
}

.button-header{
    font-size: 1.2rem;
    padding: 0 2rem;
    height: 2rem;
    background-color: #EC1D24;
    border: 2px solid #fff; 
    color: #fff;
    border-radius: 0.5rem;
    cursor: pointer;

    display: -webkit-flex;

    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;

    transition: 0.5s;
}

.button-header:hover{
    color:  #EC1D24;
    background-color: #fff;
    border: 2px solid #EC1D24
}
.search-comic {
    height: 70px;
    display: block;
    padding: 10px;
    font-size: 20px;
    border-radius: 5px;
    width: 40%;
    margin: 20px auto;
    margin-top: 150px;
}
.modal-comic{
    background-color: #fff;
    border-radius: 4px;
    width: 800px;
    margin: 80px auto 0 auto;
    padding: 15px;
}

.modal__overlay{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0, 0.5);

}

.modal__close-button{
    float: right;
    font-size: 1.4rem;
    width: 2rem;
    height: 2rem;
    background: none;
    border: unset; 
    color: #000;
    border-radius: 0.5rem;
    cursor:pointer;
}

.imgComic-modal{
    height: 600px;
    width: 400px;
    display: -webkit-flex;
    display: flex;
    margin:auto ;
}

.modal-title{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin-bottom: 10px;
    margin-left: auto;
    margin-right: auto;
}
.title-comic{
    padding:5px;
    text-align: justify;
    max-width: 270px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

.img-card-cart{
    max-height: 427px;
    
}
.msg-cart-empty{
    margin-top: 135px;
    position: unset !important;
}

.msg-cart{
    margin-top: 20px;
    position: unset !important;
}

.card-cart{
    position: unset !important;
}

.btn-send{
    height: 40px;
    width: 200px;
    margin-top: 135px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 3px;
    border: 0;
    cursor: pointer;
    text-align: center;
    font-size: 1.2rem;
    text-decoration: none;
    background-color: #EC1D24;
    border-radius: 10px;
    color: #ffffff;
    font-weight: 800;
}

.btn-send:hover{
    color: #ffffff;
    background-color: #db0b12;
    text-decoration: none;
}
.map-container{
    top: 21rem;
    width: 100vw;
    height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center
}

.search {
    position: absolute;
    top: 12rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    max-width: 400px;
    z-index: 10;
}
.search input{
    padding: 0.5rem;
    font-size: 1.2rem;
    width: 100%;
}

#page-map {
    width: 100vw;
    height: 100vh;
  }
  
  form.landing-page-form {
    width: 500px;
  
    background: #ffffff;
    border: 1px solid #d3e2e5;
    border-radius: 30px;
  
    padding: 20px 40px;
  
    position: absolute;
    top: 150px;
    left: 50px;
  
    z-index: 1;
  }
  
  #page-map .leaflet-container {
    z-index: 0;
  }
  
  form.landing-page-form fieldset {
    border: 0;
  }
  
  form.landing-page-form fieldset legend {
    width: 100%;
  
    font-size: 32px;
    line-height: 34px;
    color: #5c8599;
    font-weight: 700;
  
    border-bottom: 1px solid #d3e2e5;
    margin-bottom: 20px;
    padding-bottom: 20px;
  }
  
  form.landing-page-form .input-block + .input-block {
    margin-top: 24px;
  }
  
  form.landing-page-form .input-block label {
    display: -webkit-flex;
    display: flex;
    color: #8fa7b3;
    margin-bottom: 8px;
    line-height: 24px;
  }
  
  form.landing-page-form .input-block label span {
    font-size: 14px;
    color: #8fa7b3;
    margin-left: 24px;
    line-height: 24px;
  }
  
  form.landing-page-form .input-block input {
    width: 100%;
    background: #f5f8fa;
    border: 1px solid #d3e2e5;
    border-radius: 20px;
    outline: none;
    color: #5c8599;
  }
  
  form.landing-page-form .input-block input {
    height: 44px;
    padding: 0 16px;
  }
  
  form.landing-page-form button.confirm-button {
    margin-top: 34px;
  
    width: 100%;
    height: 64px;
    border: 0;
    cursor: pointer;
    background-color: #EC1D24;
    border-radius: 20px;
    color: #ffffff;
    font-weight: 800;
  
    transition: background-color 0.2s;
  }
  
  form.landing-page-form button.confirm-button:hover {
    background-color: #6c79f5;
  }
  
  /* Pop Up - Marker */
  
  #page-map .map-popup .leaflet-popup-content-wrapper {
    background: rgba(255, 255, 255, 0.8);
    border-radius: 20px;
    box-shadow: none;
  }
  
  #page-map .map-popup .leaflet-popup-content h3 {
    color: #0089a5;
    font-size: 20px;
    font-weight: bold;
    margin: 8px 12px;
  }
  
  #page-map .map-popup .leaflet-popup-content p {
    color: #042f38;
    font-size: 12px;
    font-weight: bold;
    margin: 8px 12px;
    line-height: 15px;
  }
  
  #page-map .map-popup .leaflet-popup-tip-container {
    display: none;
  }
  
  /* Styling react select */
  
  .filter__control {
    border-radius: 20px !important;
  
    width: 100% !important;
    background: #f5f8fa !important;
    border: 1px solid #d3e2e5 !important;
    border-radius: 20px !important;
    outline: none !important;
    color: #5c8599 !important;
  }
  
  .filter__option {
    background: #f5f8fa !important;
    color: #5c8599 !important;
  }
  
  .filter__option--is-focused {
    background: #d3e2e5 !important;
    color: #010101 !important;
  }
.messages {
    position: absolute;
    top:100px;
    right: 20px;
    z-index: 5;
}
* {
    margin: 0 ;
    padding: 0; 
    box-sizing: border-box;
    outline: 0;
    font-family: 'Marvel', sans-serif;
}
